* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Spartan", sans-serif;
}

body {
  background-color: white;
  background-color: white;
  padding: 80px 25px;
  position: relative;
  min-height: 100vh;
}
@media screen and (min-width: 1050px) {
  body {
    padding: 130px 25px;
  }
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media screen and (min-width: 1050px) {
  main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 35px;
    max-width: 1110px;
    min-height: 575px;
    margin: 0 auto;
  }
}

.attribution {
  position: absolute;
  bottom: 0%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 35px;
}
@media screen and (min-width: 1050px) {
  .info {
    align-items: start;
    gap: 15px;
    margin: 0;
    max-height: 225px;
  }
}
.info__title {
  text-align: center;
  color: #502050;
  font-size: 30px;
  font-weight: 700;
  letter-spacing: -1px;
  max-width: 80%;
}
@media screen and (min-width: 1050px) {
  .info__title {
    font-size: 50px;
    letter-spacing: -3px;
    line-height: 0.95;
    text-align: left;
    max-width: 80%;
  }
}
.info__description {
  text-align: center;
  color: #937b92;
  font-size: 14px;
  line-height: 1.7;
  font-weight: 500;
}
@media screen and (min-width: 1050px) {
  .info__description {
    text-align: left;
    font-size: 15px;
    letter-spacing: -1px;
    line-height: 1.7;
    max-width: 80%;
  }
}

.ratings {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15px;
  margin-bottom: 50px;
}
@media screen and (min-width: 1050px) {
  .ratings {
    display: grid;
    grid-template-columns: repeat(11, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 20px;
    padding: 15px 0;
    margin-bottom: 0px;
    max-height: 225px;
  }
}
.ratings__item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 15px 0;
  align-items: center;
  background-color: #f7f2f7;
  border-radius: 10px;
}
@media screen and (min-width: 1050px) {
  .ratings__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 30px;
    padding: 16px 0;
    padding-left: 30px;
    align-items: center;
    background-color: #f7f2f7;
    border-radius: 10px;
  }
  .ratings__item img {
    margin-right: 3px;
  }
  .ratings__item:first-child {
    grid-row: 1/2;
    grid-column: 1/span 9;
  }
  .ratings__item:nth-child(2) {
    grid-row: 2/3;
    grid-column: 2/span 9;
  }
  .ratings__item:last-child {
    grid-row: 3/4;
    grid-column: 3/span 9;
  }
}
.ratings__item p {
  font-size: 13px;
  color: #502050;
  font-weight: 700;
}

.card-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
@media screen and (min-width: 1050px) {
  .card-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 15px;
    grid-column: 1/-1;
  }
}
.card-section img {
  border-radius: 50%;
  max-width: 100%;
}
.card-section .card {
  display: grid;
  grid-template-columns: 40px 1fr;
  grid-template-rows: 40px 1fr;
  grid-template-areas: "img user" "review review";
  gap: 25px 20px;
  max-width: 325px;
  padding: 40px 30px;
  background-color: #502050;
  border-radius: 7px;
}
@media screen and (min-width: 1050px) {
  .card-section .card {
    max-width: 350px;
    min-height: 233px;
  }
  .card-section .card:first-child {
    align-self: start;
  }
  .card-section .card:nth-child(2) {
    align-self: center;
  }
  .card-section .card:last-child {
    align-self: end;
  }
}
.card-section .card__img {
  grid-area: img;
}
.card-section .card__user {
  grid-area: user;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
}
.card-section .card__user h4 {
  font-size: 13px;
  color: white;
}
.card-section .card__user p {
  font-size: 13px;
  color: #ee68a4;
}
.card-section .card__review {
  grid-area: review;
  color: white;
  font-size: 13px;
  line-height: 1.6;
}